
export const monthToShortString = (month: number | undefined) => {
    if (!month) return ``;
    const mapping = [
        `Jan`, `Feb`, `Mar`, `Apr`, `Jun`, `Jul`, `Aug`, `Sep`, `Oct`, `Nov`, `Dec`
    ]
    return mapping[month - 1]
}

const notUndefined = <T>(x: T | undefined): x is T => {
    return x !== undefined;
}

export const joinStringsWithDot = (strs: (string | undefined)[]) => {
    return strs
        .filter(notUndefined)
        .filter(s => s.length > 0)
        .join(` · `)
}